const studentsList = () => import('./students-list')
const studentsAdd = () => import('./students-view')
export default [
    {
        path: '/students-list',
        name: 'students-list',
        meta: {
            requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
        },
        component: studentsList,
    },
    {
        path: '/students-add',
        name: 'students-add',
        meta: {
            requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
        },
        component: studentsAdd
    }

]
