/**
 * 路由模块化，模块内路由命名必须是index.js
 */
const manageFiles = require.context('../', true, /index\.js$/)
let configRouters = []
manageFiles.keys().forEach(key => {
    if(key == './router/index.js')return    //不包含根文件
    configRouters = configRouters.concat(manageFiles(key).default) // 读取出文件中的default模块
})

export default configRouters
