// 引入
import axios from 'axios'
import qs from 'qs'
import {Modal, Notice} from 'iview'
import global_ from '../config/Global'//引用文件
import storage from 'good-storage'
import router from '@/router'

//mock地址
const mockUrl = 'http://127.0.0.1:8080/app/mock/110/';

//服务器地址(判断是生产环境还是开发环境)
const serverUrl = global_.serverURL;

//axios默认参数配置
axios.defaults.timeout = 1000 * 50;
axios.defaults.withCredentials = true;

//添加一个请求拦截器
axios.interceptors.request.use(function (config) {
    if (config.method === "POST") {
        config.data = JSON.stringify(config.data);
        config.headers['Content-Type'] = 'application/json';
    }
    config.headers['token'] = storage.get("token",'');
    return config;
}, function (error) {
    return Promise.reject(error);
});

// 添加一个响应拦截器
axios.interceptors.response.use(function (res) {
    // let response = res.response;

    let response = res;
    console.log(response)
    return new Promise((resolve) => {

        if (response.data.code === '0') {
            if (response.data.message) {   // 为null
                // Notice.success({title: response.message ? response.message : ' '});
            }
            resolve(response.data);
        }else if (response.data.code === '1'){
            Notice.error({
                title: '系统提示',
                desc: response.data.message ? response.data.message : '网络繁忙！',
                // duration: 0
            });
            resolve(false);
        } else {
            Notice.error({
                title: '系统提示',
                desc: response.data.message ? response.data.message : '网络繁忙！'
            });
            resolve(false);
        }
    }).catch(error => {

    });
}, function (error) {
    const res = error.response;
    if (res.status === 401) {     //token失效状态码
       // window.parent.postMessage('refresh', '*');
        router.push({
            path: '/'
        });
    } else {
        return Promise.reject(error);
    }
});

//判断是否为mock地址
export default function request(url, option) {
    let httpUrl;
    if (!option.noPrefix) {
        httpUrl = option.mock ? mockUrl + url : serverUrl + url;
    } else {
        httpUrl = option.mock ? mockUrl + url : url;
    }
    let reqUrl = '';
    if (option.body.method === 'GET') {
        reqUrl = httpUrl + (option.body.data ? '?' + qs.stringify(option.body.data) : '');
    } else {
        option.body.method = 'POST';
        reqUrl = httpUrl;
    }
    const defaultOptions = {
        withCredentials: false,
        method: option.body.method,
        url: reqUrl
    };

//插件axios调用
    const newOptions = {...defaultOptions, ...option.body};
    return axios({
        ...newOptions
    }).then(res => {
        return res
    })
}
