const Home = () => import('./Home')
const Login = () => import('./Login')
export default [
    {
        path: '/',
        name: 'login',
        component: Login
    }, {
        path: '/home',
        name: 'home',
        meta: {
            requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
        },
        component: Home
    },
]
